* {
	font-family: "Raleway", sans-serif;
}

body {
	margin-inline: auto;
	height: 100vh;
	background-color: #f9f9f9;
	font-weight: 200;
	font-family: "Raleway", sans-serif;
}

.site-container {
	display: flex;
	flex-direction: column;
}

.link,
a:visited,
a:link {
	text-decoration: none;
	color: inherit;
}

.neck {
	display: flex;
	justify-content: space-around;
	width: 100%;
	margin-top: 75px;
}

.projects-view-wrap {
	display: flex;
	justify-content: space-around;
}

.main-content {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: space-evenly;
}

.current-view {
	font-size: 2em;
	width: 70%;
	text-align: center;
}

.project-info {
	width: 70%;
	height: 100%;
	border-radius: 10px;
	box-shadow: 8px 10px 27px -1px rgba(0, 0, 0, 0.12);
}

.contact-wrap {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.about-me {
	text-align: center;
}

@media only screen and (max-width: 768px) {
}

@media only screen and (max-width: 375px) {
  body {
    margin: 0;
    /* height: 100vh; */
  }

	.neck {
    justify-content: center;
  }
}
