.main-view {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-family: 'Changa', sans-serif; */
}

.landing-title {
  position: relative;
  font-family: 'Changa', sans-serif;

}

.landing-circle {
  display: flex;
  /* border: 1px solid green; */
  height: 300px;
  width: 300px;
  border-radius: 50%;
  /* z-index: 5; */
  overflow: hidden;
  background-color: #F4C7B8;
}

.wave-1 {
  position: relative;
  height: 100px;
  top: 204px;
  transform: rotate(180deg) scale(35);
  z-index: 5;
  overflow: hidden;
  filter: invert(72%) sepia(70%) saturate(7459%) hue-rotate(349deg) brightness(94%) contrast(85%);
}

